import React from "react";
import { Container, Row, Col } from "react-bootstrap";

import img1 from "../assets/img/icons/solution1.svg";
import img2 from "../assets/img/girls.png";

const CtaThree = () => {
  return (
    <section className="pt-120 pb-120">
      <Container>
        <Row>
          <Col lg={5} sm={5}>
            <div className="user-interact-inner">
              <h2>Se summering fr alla dina barn p ett stlle</h2>
              <p>
                Vi har rknat, det tar flera minuter att f en uppfattning om vad som hnder i skolan imorgon fr dina barn. I vr app kan du direkt se allt som r aktuellt fr alla barnen p en och samma skrm.
              </p>
              <a href="#" className="btn">
                App Store
              </a>
            </div>
          </Col>
          <Col lg={7} sm={7}>
            <div className="user-interact-image type2">
              <img src={img2} alt="" />
            </div>
          </Col>
        </Row>
      </Container>
    </section>
  );
};

export default CtaThree;
